.officeWrapper {
  padding: 40px 20px;
  background: #9BC7CF;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-radius: 3px;
}

.loggingHeader {
  font-size: 20px;
  line-height: 36px;
  text-align: center;
  color: #1E3139;
  white-space: pre-line;
  text-transform: uppercase;
  font-weight: normal;
  margin-bottom: 35px;
  background: url('../../assets/img/icons/user-gray-office-back.svg');
  background-repeat: no-repeat;
  background-size: 80px 80px;
  width: 100%;
  padding: 15px 0;
  background-position: 10% center;
}

@media(min-width: 1200px) {
  .officeWrapper {
    height: 100%;
    overflow-y: auto;
    background: url('../../assets/img/background/officeBackgrund.jpg');
    background-image: -webkit-image-set(url('../../assets/img/background/officeBackgrund.webp') 1x, url('../../assets/img/background/officeBackgrund.jp2') 1x, url('../../assets/img/background/officeBackgrund.jpg') 1x);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    padding: 20px;
    min-height: 400px;
  }

  .loggingHeader {
    font-size: 28px;
    line-height: 36px;
    background-position: 20% center;
    margin-bottom: 15px;
  }
}
